<!--  -->
<template>
    <div class='listBox'>
        <div class='list' v-for='(item,index) in list'  :key='index' @click='detailsName(item.productId)' >
            <img :src="item.picture" alt="" class='list-img'>
            <p class='list-name'>{{item.title}}</p>
            <div class='list-money'>
                <span class='money1'>￥{{item.sellPrice}}</span>
                <span class='money2' v-if='item.marketPrice!=null' >￥{{item.marketPrice}}</span>
            </div>
        </div>
    </div>
</template>

<script>
    //这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
    //例如：import 《组件名称》 from '《组件路径》';
    import {shopList} from '../../api/api'
    import { mapActions } from 'vuex'
    export default {
    //import引入的组件需要注入到对象中才能使用
    components: {

    },
    data() {
    //这里存放数据
    return {
        list:[]
    };
    },
    //监听属性 类似于data概念
    computed: {},
    //监控data中的数据变化
    watch: {},
    //方法集合
    methods: {
        ...mapActions(['headTitle','showBtm','goBack']),
         // 跳转详情页
        detailsName:function(id){
            this.$router.push('/shopDetails/'+id)
        },
    },
    //生命周期 - 创建完成（可以访问当前this实例）
    created() {
        this.headTitle('商品列表')
        this.showBtm(false)
        this.goBack(true)
    },
    //生命周期 - 挂载完成（可以访问DOM元素）
    mounted() {
        this.loading = this.$toast.loading();
        shopList(this.$route.params.id).then(response=>{
            this.loading.hide();
            this.list=response.data.data.showProductList
        });
    },
    beforeCreate() {}, //生命周期 - 创建之前
    beforeMount() {}, //生命周期 - 挂载之前
    beforeUpdate() {}, //生命周期 - 更新之前
    updated() {}, //生命周期 - 更新之后
    beforeDestroy() {}, //生命周期 - 销毁之前
    destroyed() {}, //生命周期 - 销毁完成
    activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
    }
</script>
<style lang='scss' scoped>
    //@import url(); 引入公共css类
    .listBox{
        width:95%;
        margin:0 auto;
        display:flex;
        justify-content:space-between;
        flex-wrap: wrap;
        margin-top:110px;
    }
    .list{
        width:347px;
        height:517px;
        background:#fff;
        border-radius:10px;
        margin-bottom:16px;
        overflow: hidden;
    }
    .list-img{
        width:347px;
        height:347px;
        display:block;
        margin-bottom:16px;
    }
    .list-name{
        width:100%;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size:26px;
        color:#000;
        font-weight:bold;
        height:60px;
        line-height:60px;
        margin-bottom:18px;
        padding-left:24px;
    }
    .list-money{
        display:flex;
        align-items:center;
        padding-left:24px;
    }
    .money1{
        font-size:36px;
        color:rgb(255,0,0);
        margin-right:20rpx;
    }
    .money2{
        font-size:24px;
        color:#ccc;
    }
</style>



